/*create variable*/
:root {
    --primary: #2EA393;
    --secondary: #F09873;
    --dark: #5F706D;
    --light: #F1F1F1;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body {
    background-color: var(--light);
}

.App {
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
}

.calculator {
    width: 100%;
    max-width: 400px;
    background-color: white;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 2px 64px rgba(49, 48, 48, 0.5);
}

.display {
    padding: 18px;
    text-align: right;
    background-color: var(--dark);
    color: var(--light);
    font-size: 26px;
    font-weight: 300;
}

.display span {
    font-size: 15px;
    color: rgb(173, 171, 171);
}

button {
    appearance: none; /* no default styles*/
    border: none;
    /*box-shadow: 0px 0px 45px 7px rgba(212,212,212,0.31) ;*/
    color: white;
    font-size: 40px;
    padding: 15px;
    /*margin: .4px;*/
    cursor: pointer;
    transition: 0.4s;
}

button:hover {
    opacity: 0.9;
}

.operators {
    display: flex;
}

.operators button {
    flex: 1 1 0%; /* makes buttons equal*/
    background-color: var(--primary);
    font-weight: 700;
}

.digits {
    display: flex;
    flex-wrap: wrap;
}

.digits button {
    flex: 1 1 33.333%; /* stretches to fit */
    max-width: 33.333%; /* to be sure buttons are fully stretched*/
    background-color: var(--dark);
}